<template>
     <b-modal id="SelectLocation" hide-header centered size="lg" no-close-on-backdrop hide-footer content-class="model-bg-gray">
         <div  class="text-center mb-5"></div>
         <div class="d-flex justify-content-around">
             <div v-for="(item,index) in location.data" :key="index" class="text-center" >
                 <div class="locationIcon mb-2" v-html="item.icon" :class="{active : index == location.index}" @click="location.index = index">
                 </div>
                 <span class="gray-text">{{item.text}}</span>
             </div>
         </div>
         <div class="mt-5 d-flex justify-content-center">
              <div class="mybtn blueStyle" style="margin:0;" @click="submit()"></div>
         </div>
    </b-modal>
</template>
<script>
    import Vue from 'vue'
    import firebase from 'firebase/app'
    import 'firebase/messaging'
    import { firestorePlugin } from 'vuefire'
    import { mapState } from 'vuex'
    const RegistrationTopicURL = '/API/RegistrationTopicV2'
    Vue.use(firestorePlugin)

    export default {
        data() {
            return {
                token: '',
                AccountID: '',
                location: {
                    index: 0,
                    data:[
                    {
                        text: '',
                        value: '',
                        icon: `<svg width="11" height="50" viewBox="0 0 11 50" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M0.71475 50H9.47044V15.4747H0.71475V50ZM5.12237 9.92602C8.1005 9.92602 10.1852 7.95314 10.1852 4.93218C10.1852 1.97287 8.1005 0 5.12237 0C2.08469 0 0 1.97287 0 4.93218C0 7.95314 2.08469 9.92602 5.12237 9.92602Z" fill="#93959C"/></svg>`
                    },
                    {
                        text: '',
                        value: '',
                        icon: `<svg width="28" height="50" viewBox="0 0 28 50" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M11.7312 43.4516C9.47913 43.4109 7.28849 43.2273 5.15927 42.9009C3.03005 42.5337 1.31029 42.0441 0 41.4321V36.2301C1.39218 36.8829 3.17336 37.4541 5.34353 37.9437C7.5137 38.4333 9.64292 38.6985 11.7312 38.7393V26.377C7.67748 25.2754 4.70886 23.8882 2.82531 22.2154C0.941771 20.5426 0 18.2579 0 15.3611C0 12.3419 1.04414 9.97552 3.13241 8.26194C5.26163 6.54835 8.12789 5.58956 11.7312 5.38556V0H15.6621V5.32436C17.8322 5.36516 19.7977 5.60996 21.5584 6.05875C23.36 6.46675 24.9979 6.97675 26.472 7.58874L24.8136 12.1175C23.5033 11.5871 22.0497 11.1383 20.4528 10.7711C18.8968 10.3631 17.2999 10.0979 15.6621 9.97552V22.2766C18.3645 23.0518 20.6166 23.8882 22.4182 24.7858C24.2199 25.6426 25.5711 26.7238 26.472 28.0294C27.4137 29.2942 27.8846 30.9466 27.8846 32.9865C27.8846 35.8425 26.82 38.1681 24.6908 39.9633C22.5616 41.7177 19.552 42.7989 15.6621 43.2069V50H11.7312V43.4516ZM11.7312 10.0979C9.52008 10.2611 7.88222 10.7711 6.8176 11.6279C5.79394 12.4847 5.28211 13.5863 5.28211 14.9327C5.28211 16.5647 5.73252 17.8295 6.63334 18.7271C7.57512 19.5838 9.2744 20.3182 11.7312 20.9302V10.0979ZM15.6621 38.4333C18.0779 38.1885 19.8386 37.6377 20.9442 36.7809C22.0497 35.8833 22.6025 34.7409 22.6025 33.3537C22.6025 31.8033 22.1111 30.6406 21.1284 29.8654C20.1457 29.0494 18.3236 28.315 15.6621 27.6622V38.4333Z" fill="#93959C"/></svg>`
                    },
                    {
                        text: '',
                        value: '',
                        icon: `<svg width="44" height="50" viewBox="0 0 44 50" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M19.4444 10V15C16.5834 15.0001 13.7857 15.8658 11.3995 17.4893C9.01325 19.1128 7.14403 21.4223 6.0245 24.1304C4.90498 26.8385 4.58462 29.8255 5.10332 32.7195C5.62201 35.6134 6.95685 38.2866 8.94162 40.406C10.9264 42.5255 13.4734 43.9976 16.2656 44.6392C19.0578 45.2807 21.9718 45.0634 24.6447 44.0141C27.3177 42.9649 29.6315 41.1301 31.2982 38.7383C32.9649 36.3464 33.9108 33.5032 34.0181 30.5625L34.0278 30H38.8889C38.8889 35.3043 36.8403 40.3914 33.1937 44.1421C29.5472 47.8929 24.6014 50 19.4444 50C14.2875 50 9.34169 47.8929 5.69515 44.1421C2.04861 40.3914 0 35.3043 0 30C0 19.1625 8.70139 10 19.4444 10ZM43.75 0V5L30.8024 20H43.75V25H24.3056V20L37.2507 5H24.3056V0H43.75Z" fill="#93959C"/></svg>`
                    },
                    {
                        text: '',
                        value: '',
                        icon: `<svg width="38" height="50" viewBox="0 0 38 50" fill="none" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" clip-rule="evenodd" d="M25.586 29.7243L33.6786 15.4692C35.9485 11.4707 34.5816 6.38071 30.7019 4.10266C26.8526 1.84247 21.9068 3.16276 19.6533 7.13242L11.5045 21.4865L25.586 29.7243ZM17.0443 5.65135C20.1123 0.247162 26.9071 -1.60446 32.2209 1.51565C37.5348 4.63576 39.3554 11.5461 36.2875 16.9503L20.7334 44.3487C17.6655 49.7528 10.8707 51.6045 5.55687 48.4843C0.243025 45.3642 -1.57763 38.4539 1.49032 33.0497L17.0443 5.65135Z" fill="#93959C"/></svg>`
                    },
                    ]
                },
                storageLocation:''
            }
        },
        computed: {   // store.js  state.status & state.MemberPhone
            ...mapState(['CTI'])
        },
        created() {
            let _this = this

             var firebaseConfig = {
                    apiKey: "AIzaSyBUx8ZI0IqIUYWEcNCNcLkLsKRWcw_cbl4",
                    authDomain: "cti-project-6b1c8.firebaseapp.com",
                    databaseURL: "https://cti-project-6b1c8.firebaseio.com",
                    projectId: "cti-project-6b1c8",
                    storageBucket: "cti-project-6b1c8.appspot.com",
                    messagingSenderId: "821741039307",
                    appId: "1:821741039307:web:398ed29fa761711e59b54e",
                    measurementId: "G-YD9F5TEBSG"
             };

             if (!firebase.apps.length) {
                firebase.initializeApp(firebaseConfig);
                Vue.prototype.$messaging = firebase.messaging()
                Vue.prototype.$messaging.usePublicVapidKey('BCY4QAyqeygt_kXbWVL8xzxIqBcTk87Uxm_l8htUYz-CMoToYAsC4gT0XBmM2_GzSTF6NcqBdadusW9rJ96kAMY')
             }

             if ('serviceWorker' in navigator) {
                    navigator.serviceWorker
                        .register('../../serviceWorker.js') //  Service Worker
                        .then(function (reg) {

                            console.log('Registration succeeded. Scope is ' + reg.scope); // 
                        })
                        .catch(function (error) {
                            console.log('Registration failed with ' + error); // 
                        });
             }

             Vue.prototype.$messaging.requestPermission().then(() => {
                // notification permissin granted
                Vue.prototype.$messaging.getToken().then((currentToken) => {
                    //  token
                    console.log(currentToken)
                    this.token = currentToken
                    this.$store.dispatch('handSetGlobalToken', currentToken)

                });
             });

             Vue.prototype.$messaging.onMessage(function (payload) {
                let Info = payload.notification
                if (Notification.permission === 'granted') {
                    _this.ShowNotification(Info.title, Info.body, Info.icon, Info.click_action);
                }
             });
        },
        mounted() {
            let myServicePhone = this.$route.params.ServicePhone

            //storeLOCATION
            this.storageLocation = localStorage.getItem("Location");

            let storage = JSON.parse(localStorage.getItem("UserInfo"));

            let deptID = storage.deptID1


            if (myServicePhone == '0800668660' || myServicePhone == '0800747488') {
                let _this = this
                let AccountID = this.AccountID = storage.accountID

                setTimeout(() => {
                     let Token = _this.token
                     let ServicePhoneID = _this.CTI.MyNumbers[0].servicePhoneID
                     localStorage.setItem("Location", "")
                    _this.RegistrationTopic(AccountID,Token,"Add","",ServicePhoneID)
                },2000)
            } else if ((myServicePhone !== '0800668660' || myServicePhone !== '0800747488') && deptID !== "G" && !this.storageLocation ) {
                 this.$bvModal.show('SelectLocation') 
            }

        },
        watch: {
            
        },
        methods: {
            submit() {
                let i = this.location.index
                let Location = this.location.data[i].value
                let storage = JSON.parse(localStorage.getItem("UserInfo"));
                let AccountID = this.AccountID = storage.accountID
                let Token = this.token
                let ServicePhoneID = this.CTI.MyNumbers[0].servicePhoneID
                localStorage.setItem("Location", Location)
                this.RegistrationTopic(AccountID,Token,"Add",Location,ServicePhoneID)
            },
            RegistrationTopic(AccountID,Token,batchEvent,Location,ServicePhoneID) {
                //batchEvent Add() OR Remove()
                fetch(RegistrationTopicURL, {
                    headers: {
                            'Accept': 'application/json',
                            'Content-Type': 'application/json'
                        },
                    body: JSON.stringify({ AccountID: AccountID,Token:Token,batchEvent:batchEvent,Location:Location,ServicePhoneID:ServicePhoneID }),
                    method: 'POST',
                }).then((response) => {
                    return response.json();
                }).then((jsonData) => {
                    this.$bvModal.hide('SelectLocation')
                    this.$store.dispatch('handSetGlobalLocation', Location)
                    return jsonData
                }).catch(err => {
                    this.$bvModal.hide('SelectLocation')
                    console.log(err)
                })
            },
            ShowNotification(title, body, icon, action) {
                navigator.serviceWorker.ready.then(function (registration) {
                    registration.showNotification(title, {
                        body: body,
                        icon: icon,
                        tag: title,
                        click_action: action
                    });
                });
            },
        }
    }
</script>
<style>
    #SelectLocation .locationIcon {
        width: 120px;
        height: 120px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #505359;
        border-radius: 10px;
        cursor:pointer;
        transition:.3s;
    }
       

        #SelectLocation .locationIcon.active {
            background: #505359;
        }

        #SelectLocation .modal-body {
            display: flex;
            flex-direction: column;
            justify-content: center;
            border-radius: 25px;
            padding: 50px;
        }
</style>