<template>

    <b-modal id="popup" ref="CallInPopup" centered title="" hide-footer size="md" footer-class="model-bg-gray" content-class="model-bg-gray">
        <b-tabs content-class="mt-3" vertical>
            <b-tab :active="item.status == 3 || item.status == 4"
                   :disabled="item.status == 0"
                   :key="indexsss"
                   v-for="(item,indexsss) in CTI.AllInfo">
                <template #title>
                    <div class="w-100 d-flex align-items-center justify-content-between" v-if="item.status == 3 || item.status == 4" :class="{green :item.status == 3,blue : item.status == 4}">
                        <div class="locationIcon">
                            <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <circle cx="15" cy="15" r="14.5" stroke="#93959C" />
                                <rect x="11.5" y="8.5" width="8" height="16" rx="1.5" stroke="#93959C" />
                                <path d="M13 18H14" stroke="#93959C" />
                                <path d="M13 20H14" stroke="#93959C" />
                                <path d="M13 22H14" stroke="#93959C" />
                                <path d="M15 18H16" stroke="#93959C" />
                                <path d="M15 20H16" stroke="#93959C" />
                                <path d="M15 22H16" stroke="#93959C" />
                                <path d="M17 18H18" stroke="#93959C" />
                                <path d="M17 20H18" stroke="#93959C" />
                                <path d="M17 22H18" stroke="#93959C" />
                                <path d="M13 5.5C13 5.22386 13.2239 5 13.5 5C13.7761 5 14 5.22386 14 5.5V9H13V5.5Z" fill="#93959C" />
                            </svg>
                        </div>
                        <div class="text-center">
                            {{item.location}} #{{item.agentNo}}
                        </div>
                        <b-spinner type="grow"
                                   small
                                   :class="{green :item.status == 3,blue : item.status == 4}">
                        </b-spinner>
                    </div>

                </template>
                <div class="CallToWhere">
                    <span :class="{'gray-text' : item.displayName == item.cdisplayName,'orange-text' :item.displayName !== item.cdisplayName }">
                        {{item.displayName == item.cdisplayName ? item.displayName :  item.cDisplayName}}
                    </span>
                    <span style="padding:0 10px;">></span>
                    <span class="white-text">
                        #{{item.number}} {{item.title}}
                    </span>
                </div>
                <div class="mb-3 d-flex justify-content-around" v-if="item">
                    <div class="d-flex flex-column justify-content-center" style="width: 130px;">
                        <div class="mb-4 d-flex justify-content-center" :class="{'blue-text' : item.status == 3 , 'green-text' : item.status == 4 , 'white-text' : item.status == 0}">
                            {{item.status == 3 ? '' : item.status == 4 ? '' : '' }}
                        </div>
                        <div class="CallInIcon mb-4 mt-2 Dial" v-if="item.status == 3">
                            <img width="24" height="22" src="../assets/CallInStatusIcon.png" />
                        </div>
                        <div class="CallInIcon mb-4 mt-2 Up" v-else-if="item.status == 4">
                            <img width="24" height="22" src="../assets/CallInStatusIcon.png" />
                        </div>
                        <div class="CallInIcon mb-4 mt-2 HandUp" v-else-if="item.status == 0">
                            <img width="24" height="22" src="../assets/CallInStatusIcon.png" />
                        </div>
                        <div class="text-center">
                            <div v-for="(itemss , index) in LastCellPhone.data" :key="index">
                                <div v-for="(itemsss , indexs) in itemss" :key="indexs">
                                    <div v-if="index == indexsss" class="text-center">
                                        <br>
                                        {{itemsss.tagName != '' ? itemsss.tagName : '- -'}}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>


                    <div class="d-flex flex-column align-items-start justify-content-around" style="width:380px;" :class="{'justify-content-center': !tagName.data}">
                        <div v-if="item.status == 3 || item.status == 4">
                            <div class="mb-1">
                                <span class="WhoCaller mr-2" :class="{ blue : item.customerID == 0, orange:  item.customerID == null, white : item.customerID !== null &&  item.customerID !== 0}">{{item.customerID == 0 ? '' : item.customerID == null ? '' : item.name}}</span>
                                <span>{{item.sex == 1 ? '' : item.sex == 2 ? '' : ''}} <span v-if="item.age">({{item.age}})</span></span>
                            </div>
                            <div class="fromNumber mb-3">{{item.fromPhoneNo}}</div>
                        </div>
                        <div class="mb-4">
                            <div v-for="(items , index) in tagName.data" class="tagNameWrap " :key="index">
                                <div v-for="(itemss , indexs) in items" :key="indexs">
                                    <div class="tagName normalTag" v-if="index == indexsss">
                                        <svg width="12" height="13" viewBox="0 0 12 13" fill="none" xmlns="http://www.w3.org/2000/svg">
                                            <path d="M12 4.77254V1.0043C11.972 0.428556 11.5207 0.00693333 11.023 0H7.35701C6.76746 0.0376566 6.06356 0.275242 5.68552 0.713808L0.259268 6.92227C-0.081584 7.36581 -0.0912275 7.97959 0.259268 8.34986L4.5066 12.7157C4.92305 13.103 5.54441 13.0865 5.89546 12.7157L11.3056 6.49065C11.7 6.01189 11.9946 5.3718 12 4.77254ZM10.4093 2.59792C10.3862 3.15811 9.95625 3.5624 9.47259 3.56903C8.92604 3.54394 8.5343 3.09614 8.52784 2.59792C8.5519 2.03585 8.98977 1.64163 9.47259 1.63511C10.0178 1.65852 10.403 2.10271 10.4093 2.59792Z" fill="#B5B7BF" />
                                        </svg>
                                        {{itemss.tagName}}
                                    </div>

                                </div>
                            </div>
                        </div>
                        <div class="d-flex" v-if="item.status == 3 || item.status == 4">
                            <div v-b-toggle.DataSidebar>
                                <div class="CallInReportBtn blueStyle"
                                     @click="getCallInData(item.callinID),
                          ChangeSideBarTab('Report')">
                                    
                                </div>
                            </div>
                            <div>
                                <div v-if="item.customerID == 0">
                                    <div class="CallerDataBtn ml-3"
                                         @click="GetCusotmerIDList(item.fromPhoneNo)" v-b-modal.modal-1>
                                        
                                    </div>
                                </div>
                                <div v-b-toggle.DataSidebar v-else-if="item.customerID == null">
                                </div>
                                <div v-b-toggle.DataSidebar v-else>
                                    <div class="CallerDataBtn ml-3"
                                         @click="GetCustomerData(item.fromPhoneNo,item.callinID),
                          ChangeSideBarTab('CustomerInfo')">
                                        
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="d-flex" v-else-if="item.status == 0">
                            <div v-b-toggle.DataSidebar>
                                <div class="CallInReportBtn blueStyle"
                                     @click="getCallInData(isTempData.callinID),
                          ChangeSideBarTab('Report')">
                                    
                                </div>
                            </div>
                            <div>
                                <div v-if="isTempData.customerID == 0">
                                    <div class="CallerDataBtn ml-3"
                                         @click="GetCusotmerIDList(isTempData.fromPhoneNo)" v-b-modal.modal-1>
                                        
                                    </div>
                                </div>
                                <div v-b-toggle.DataSidebar v-else-if="isTempData.customerID == null">
                                </div>
                                <div v-b-toggle.DataSidebar v-else>
                                    <div class="CallerDataBtn ml-3"
                                         @click="GetCustomerData(isTempData.fromPhoneNo,isTempData.callinID),
                          ChangeSideBarTab('CustomerInfo')">
                                        
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </b-tab>
        </b-tabs>
    </b-modal>
</template>
<script>
    import { mapState } from 'vuex'
    const GetTagNameUrl = '/GetDataApi/GetTagName'
    const GetLastCellPhoneUrl = '/GetDataApi/GetLastCellPhone'
    let flag = 1

    export default {
        props: ['MyAgentNo', 'AllInfoToPopUp'],
        data() {
            return {
                UserInfo: '',
                isTempData: '',
                tagName: {
                    data: [],
                    status: '',
                },
                LastCellPhone: {
                    data: [],
                    status: '',
                },
            }
        },
        computed: {
            ...mapState(['CTI']),
        },
        watch: {
            'CTI.AllInfo': function (newValue, OldValue) {
                let _this = this
                this.CTI.AllInfo.forEach((x, i) => {
                    if (x.fromPhoneNo != null && (x.status == 3 || x.status == 4)) {
                        this.$bvModal.show('popup')
                        this.LastCellPhone.data = []
                        this.tagName.data = []
                        async function GetData() {
                            await _this.GetTagName(x.fromPhoneNo)
                            await _this.GetLastCellPhone(x.fromPhoneNo)
                        }
                        GetData()
                    }
                })
            }
        },
        mounted() {
            let storage = JSON.parse(localStorage.getItem("UserInfo"));
            this.UserInfo = storage
        },
        methods: {
            closePopup() {
                this.$bvModal.hide('popup')
            },
            GetTagName(CellPhone) {
                fetch(GetTagNameUrl, {
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({ CellPhone: CellPhone }),
                    method: 'POST',
                }).then((response) => {
                    this.tagName.status = response.status
                    return response.json();
                }).then((jsonData) => {
                    this.tagName.data.push(jsonData)
                }).catch(err => {
                    return err
                })
            },
            GetLastCellPhone(CellPhone) {
                fetch(GetLastCellPhoneUrl, {
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({ CellPhone: CellPhone }),
                    method: 'POST',
                }).then((response) => {
                    this.LastCellPhone.status = response.status
                    return response.json();
                }).then((jsonData) => {
                    this.LastCellPhone.data.push(jsonData)
                }).catch(err => {
                    return err
                })
            },
            getCallInData(CallInID) {
                this.$bvModal.hide('popup')
                this.$store.dispatch('handChangeAudioRecord', '')
                this.$emit("getCallInData", CallInID)
            },
            ChangeSideBarTab(tab) {
                this.$emit("ChangeSideBarTab", tab)
            },
            GetCustomerData(fromPhoneNo, callinID) {
                this.$bvModal.hide('popup')
                this.$emit("GetCustomerData", fromPhoneNo, callinID)
            },
            GetCusotmerIDList(Cellphone) {
                this.$bvModal.hide('popup')
                this.$emit("GetCusotmerIDList", Cellphone)
            }

        }
    }
</script>
<style>
    .tagNameWrap {
        display: flex;
        flex-wrap: wrap;
    }

    #popup .modal-dialog {
        max-width: 580px;
        min-height: calc(100% - 0px) !important;
    }

    #popup .col-auto {
        padding: 0;
    }

    #popup .modal-content {
        height: 330px;
    }

    #popup .nav-tabs {
        position: absolute;
        top: -56px;
        left: -185px;
        border-bottom: 0;
        width: 185px;
        height: calc(100% + 72px);
        min-height: 330px;
        background: #1d1e24;
        border-right: 1px solid #33363D;
        padding: 10px 0;
    }

        #popup .nav-tabs .nav-link {
            display: flex;
            align-items: center;
        }



        #popup .nav-tabs .nav-link {
            color: #FCFCFC;
        }

            #popup .nav-tabs .nav-link.active, #popup .nav-tabs .nav-item.show .nav-link {
                color: #FCFCFC;
                background-color: #23262B;
                border: 0px;
            }


                #popup .nav-tabs .nav-link.active .blue {
                    color: #58D786;
                }

                    #popup .nav-tabs .nav-link.active .blue .locationIcon rect {
                        stroke: #58D786;
                    }

                    #popup .nav-tabs .nav-link.active .blue .locationIcon path {
                        stroke: #58D786;
                    }

                    #popup .nav-tabs .nav-link.active .blue .locationIcon circle {
                        stroke: #58D786;
                    }

                #popup .nav-tabs .nav-link.active .green {
                    color: #0090FF;
                }

                    #popup .nav-tabs .nav-link.active .green .locationIcon rect {
                        stroke: #0090FF;
                    }

                    #popup .nav-tabs .nav-link.active .green .locationIcon path {
                        stroke: #0090FF;
                    }

                    #popup .nav-tabs .nav-link.active .green .locationIcon circle {
                        stroke: #0090FF;
                    }

            #popup .nav-tabs .nav-link:hover, #popup .nav-tabs .nav-link:focus {
                border: 0px;
            }

            #popup .nav-tabs .nav-link .spinner-grow-sm {
                width: 10px;
                height: 10px;
            }

            #popup .nav-tabs .nav-link .spinner-grow.green {
                color: #0090FF
            }

            #popup .nav-tabs .nav-link .spinner-grow.blue {
                color: #58D786
            }



    #popup .close {
        float: left;
        font-size: 1.5rem;
        font-weight: 700;
        line-height: 1;
        color: #dadada;
        text-shadow: none;
        opacity: .5;
        margin: 15px;
        position: relative;
        left: 4px;
    }

    #popup button.close:after {
        width: 30px;
        height: 30px;
        content: '';
        position: absolute;
        background: #30323B;
        transform: translate(-27px, -3px);
        border-radius: 5px;
        z-index: -1;
    }

    #popup .modal-header {
        border-bottom: 0px;
        padding-bottom: 0;
    }

        #popup .modal-header .close {
            margin: -1rem auto -1rem -1rem;
        }

    #popup .modal-dialog {
        max-width: 580px;
    }


    .CallInIcon {
        border-radius: 50%;
        width: 50px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
    }

        .CallInIcon.Dial {
            background: #c20b0b;
            animation: hidden 1.5s forwards linear;
            animation-delay: 0.5s;
        }

        .CallInIcon.Dial {
            position: relative;
            background-color: #0090FF;
            z-index: 30;
        }

            .CallInIcon.Dial:before {
                position: absolute;
                content: " ";
                z-index: -1;
                top: 0px;
                left: 0px;
                background-color: #0090FF;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                animation-fill-mode: both;
                -webkit-animation-fill-mode: both;
                opacity: 0.6;
                -webkit-animation: pulse 1s ease-out;
                animation: pulse 1.8s ease-out;
                -webkit-animation-iteration-count: infinite;
                animation-iteration-count: infinite;
            }

        .CallInIcon.Up {
            position: relative;
            background-color: #58D786;
            z-index: 30;
        }

            .CallInIcon.Up:before {
                position: absolute;
                content: " ";
                z-index: -1;
                top: 0px;
                left: 0px;
                background-color: #58D786;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                animation-fill-mode: both;
                -webkit-animation-fill-mode: both;
                opacity: 0.6;
                -webkit-animation: pulse 1s ease-out;
                animation: pulse 1.8s ease-out;
                -webkit-animation-iteration-count: infinite;
                animation-iteration-count: infinite;
            }

        .CallInIcon.HandUp {
            position: relative;
            background-color: #6C6B6B;
            z-index: 30;
        }

    .IvrStyle {
        color: #93959C;
        background: #30323B;
        border-radius: 15px;
        padding: 4px 10px;
    }

    .CallerDataBtn {
        padding: 6px 50px;
        border: 1px solid #0090FF;
        border-radius: 15px;
        cursor: pointer;
    }

    .CallInReportBtn {
        padding: 6px 50px;
        border-radius: 15px;
        cursor: pointer;
    }




    .CallToWhere {
        color: #6C6B6B;
        font-size: 14px;
        position: absolute;
        top: -53px;
        left: 67px;
    }

    .fromNumber {
        font-size: 17px;
        color: #FCFCFC
    }

    .CallerName {
        font-size: 20px;
        color: #FCFCFC;
    }

    .WhoCaller {
        position: relative;
        font-size: 20px;
        font-weight: bold;
    }

        .WhoCaller.orange {
            color: #FF955C;
        }

        .WhoCaller.blue {
            color: #0090FF;
        }

        .WhoCaller.white {
            color: #FCFCFC;
        }

    /*  .WhoCaller::after {
            position: absolute;
            content: "";
            width: 40px;
            height: 2px;
            background: white;
            left: -70px;
            top: 55%;
            transform: translateY(-50%);
        }

        .WhoCaller::before {
            position: absolute;
            content: "";
            width: 40px;
            height: 2px;
            background: white;
            right: -70px;
            top: 55%;
            transform: translateY(-50%);
        }*/



    @-webkit-keyframes pulse {
        0% {
            -webkit-transform: scale(0.7);
            opacity: 0;
        }

        25% {
            -webkit-transform: scale(1.1);
            opacity: 1;
        }

        50% {
            -webkit-transform: scale(1.4);
            opacity: .6;
        }

        75% {
            -webkit-transform: scale(1.7);
            opacity: .3;
        }

        100% {
            -webkit-transform: scale(1.9);
            opacity: 0;
        }
    }



    @keyframes shake {
        10%, 90% {
            transform: translate3d(0px, 0, 0);
        }

        20%, 80% {
            transform: translate3d(0px, 0, 0);
        }

        30%, 50%, 70% {
            transform: translate3d(-2px, 0, 0);
        }

        40%, 60% {
            transform: translate3d(2px, 0, 0);
        }
    }
</style>