        },
        watch: {
            // selectTimeNum  + 1   //BUG 
             'datePicker.StartValue': function (newValue, oldValue) {
                if (this.selectTimeNum == 0) {
                    let FormatDate = new Date(newValue).toLocaleDateString()
                    let FormateDateToString = new Date(new Date(FormatDate).setDate(new Date(FormatDate).getDate() + 1)).toLocaleDateString()
                    this.datePicker.FinalStartValue = FormateDateToString.slice(0, 10)
                } else {
                    this.datePicker.FinalStartValue = newValue.slice(0, 10)
                }

                setTimeout(() => {
                    document.getElementsByClassName("vdatetime-input")[1].click()
                }, 300)
            },
            'datePicker.EndValue': function (newValue, oldValue) {
                 if (this.selectTimeNum == 0) {
                    let FormatDate = new Date(newValue).toLocaleDateString()
                    let FormateDateToString = new Date(new Date(FormatDate).setDate(new Date(FormatDate).getDate() + 1)).toLocaleDateString()
                    this.datePicker.FinalEndValue = FormateDateToString.slice(0, 10)
                } else {
                    this.datePicker.FinalEndValue = newValue.slice(0, 10)
                }
                this.selectTimeNum += 1 
                this.getDateTimeForCallInCategory("RangeDate", false)
            },
            'itemsReportTable.item': function (newValue, oldValue) {
                let i = newValue.length - 1
                let Diff

                if (newValue[i] && i > 0) {
                    let lastDay = newValue[i].dialDT.slice(0, 10)
                    let Today = new Date().toISOString().slice(0, 10)
                    Diff = this.DateDiff(lastDay, Today)

                }

                this.itemsReportTable.dayDiff = Diff
            },
            'CallinNoAnswerHistoryTable.item': function (newValue, oldValue) {
                let i = newValue.length - 1
                let Diff

                if (newValue[i] && i > 0) {
