                    let Today = new Date().toISOString().slice(0, 10)
                    Diff = this.DateDiff(lastDay, Today)
                }

                this.CallinNoAnswerHistoryTable.dayDiff = Diff
            },
            'NeedCallBackList.item': function (newValue, oldValue) {
                let i = newValue.length - 1
                let Diff

                if (newValue[i] && i > 0) {
