        beforeCreate() {

        },
        mounted() {
            let _this = this
            //
            let storage = JSON.parse(localStorage.getItem("UserInfo"));
            this.UserInfo = storage
            //
            this.getServicePhoneToAgentList()

            this.getDateTimeForCallInCategory('week', true)

            for (let i in _this.CTI.AllInfo) {
                if (_this.MyAgentNo.includes(_this.CTI.AllInfo[i].agentNo)) {
                    let i = _this.tabList.tabNum
                    let value = _this.tabList.list[i].value
                    let isRange = _this.tabList.list[i].isRange
                    _this.getDateTimeForCallInCategory(value, isRange)
                } else if (content == "Hangup" && _this.CTI.AllInfo.length == 0) {
                    _this.$refs.CallInPopup.closePopup()
                }
            }

        },
        methods: {
            ScrollToTable(item) {
                let Table = this.$el.querySelector("#" + item)
                Table.scrollIntoView({behavior: "smooth", block: "center", inline: "nearest"});
            },
            //
            DateDiff(sDate1, sDate2) {
                var oDate1 = new Date(sDate1);
                var oDate2 = new Date(sDate2);
                var iDays = parseInt(Math.abs(oDate1 - oDate2) / 1000 / 60 / 60 / 24); // 
                return iDays;
            },
            openStartData() {
                document.getElementsByClassName("vdatetime-input")[0].click()
            },
            ChangeSideBarTab(tab) {
                this.$store.dispatch("handChangeSideBarTab", tab)
            },
            changeStatus(Info) {
                this.$store.dispatch('handchangeStatus', Info)
            },
            //
            getServicePhoneToAgentList() {
                this.$store.dispatch('handchangeExt', MyExtList)
            },
            //
            getNoAnswerHistory(StartDate, EndDate) {
                let myServicePhone = this.$route.params.ServicePhone
                this.CallinNoAnswerHistoryTable.item = []
