                let myServicePhone = this.$route.params.ServicePhone
                this.GetNeedTransToEIPListTable.item = []
                this.GetNeedTransToEIPListTable.item = TransToEIPReservation
            },
             GetCallinRestTimeWorkList(StartDate, EndDate) {
                this.CallinRestTimeTable.item = CallinOnWestTime
            },
            // ()
            GetCustomerData(Cellphone) {
            this.$store.dispatch("handGetCustomerData", "");
                if(Cellphone == '0963356659'){
                    this.$store.dispatch("handGetCustomerData", data1);
                    this.$store.dispatch("handGetCustomerLineAtData", CallInLineATData);
                } else if(Cellphone == '0934256284'){
                    this.$store.dispatch("handGetCustomerData", data2);
                    this.$store.dispatch("handGetCustomerLineAtData", []);
                } else{
                    this.$store.dispatch("handGetCustomerData", data2);
                    this.$store.dispatch("handGetCustomerLineAtData", []);
                }
            },
            //
            getCallInData(CallInID) {
                this.$store.dispatch('handGetCallInID', CallInID)
                this.$store.dispatch('handGetCallInData', CallinData)
            },
            imgError(item) {
                item.pictureUrl = require('../assets/DefaultPic.png')
            },
            //
            GetWhichAgentDial(CallinID) {
                this.$store.dispatch('handGetWhichAgentDial', '')
                this.$store.dispatch('handGetWhichAgentDial', CallinMissAgent)
            },
            //
            GetAudioRecord(CallType,CallxxID) {
                this.$store.dispatch('handChangeAudioRecord', '')
                this.$store.dispatch('handChangeAudioRecord', '')
            },
            getCallInCategroy(index, value, isRange) {
                this.tabList.tabNum = index
                this.getDateTimeForCallInCategory(value, isRange)
            },
            //getCallInCategory
            getDateTimeForCallInCategory(tab, isRange) {
                let StartDate
                let EndDate
                if (isRange) {
                    EndDate = new Date(new Date().setDate(new Date().getDate() + 1)).toLocaleDateString();
                    if (tab == "week") {
                        StartDate = new Date(new Date().setDate(new Date().getDate() - 7)).toLocaleDateString();
                    } else if (tab == "month") {
                        StartDate = new Date(new Date().setDate(new Date().getDate() - 30)).toLocaleDateString();
                    } else if (tab == "year") {
                        StartDate = new Date(new Date().setDate(new Date().getDate() - 365)).toLocaleDateString();
                    } else if (tab == "today") {
                        StartDate = new Date().toLocaleDateString();
                    }
                } else {
                    if (tab == "thisMonth") {
                        StartDate = new Date(new Date().setMonth(new Date().getMonth())).toISOString().substring(0, 8) + '01';
                        EndDate = new Date(new Date().setMonth(new Date().getMonth() + 1)).toISOString().substring(0, 8) + '01';
                    } else if (tab == "thisYear") {
                        StartDate = new Date(new Date().setFullYear(new Date().getFullYear())).toISOString().slice(0, 4) + '/01/01';
                        EndDate = new Date(new Date().setFullYear(new Date().getFullYear() + 1)).toISOString().slice(0, 4) + '/01/01';
                    } else if (tab == "RangeDate") {
                        StartDate = this.datePicker.FinalStartValue
                        EndDate = this.datePicker.FinalEndValue
                    }
                }
               
                //
                this.getReportList(StartDate, EndDate)
                //
                this.getNoAnswerHistory(StartDate, EndDate)
                //
                this.GetNeedCallBackList(StartDate, EndDate)
                //
                this.GetCallinRestTimeWorkList(StartDate, EndDate)
                //
                this.GetNeedTransToEIPList(StartDate, EndDate)


                //BUG
                this.$refs.ReportTable.resetFilter()
                this.$refs.CallInHistoryTable.resetFilter()
                this.$refs.NeedCallBackTable.resetFilter()
                this.$refs.CallinRestTimeTable.resetFilter()
                this.$refs.GetNeedTransToEIPListTable.resetFilter()
            },
        },
        components: {
            DataTable,
            DataSidebar,
            datetime: Datetime,
        },
        computed: {
            ...mapState(['CTI']),

            // 
            todayformat: function () {
                var rightNow = new Date();
                var res = rightNow.toISOString().slice(0, 10)

                return res
            },
        },
    }
</script>
